@import url("../../../app/styles/base-params.css")

.switch input[type="checkbox"]
  --active: var(--color2)
  --active-inner: var(--color1)
  --focus: 2px transparent
  --border: var(--color2)
  --border-hover: var(--color2)
  --background: var(--color5)
  --disabled: #f6f8ff
  --disabled-inner: #e1e6f9
  -webkit-appearance: none
  -moz-appearance: none
  outline: none
  display: inline-block
  vertical-align: top
  position: relative
  margin: 0
  cursor: pointer
  border: 1px solid var(--bc, var(--border))
  background: var(--b, var(--background))
  transition: background 0.3s, border-color 0.3s, box-shadow 0.2s
  &:after
    content: ""
    display: block
    left: 0
    top: 0
    position: absolute
    transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s)
  &:checked
    --b: var(--active)
    --bc: var(--active)
    --d-o: 0.3s
    --d-t: 0.6s
    --d-t-e: cubic-bezier(0.2, 0.85, 0.32, 1.2)
  &:disabled
    --b: var(--disabled)
    cursor: not-allowed
    opacity: 0.9
    &:checked
      --b: var(--disabled-inner)
      --bc: var(--border)
    + label
      cursor: not-allowed
  &:hover:not(:checked):not(:disabled)
    --bc: var(--border-hover)
  &:focus
    box-shadow: 0 0 0 var(--focus)
  + label
    font-size: 14px
    line-height: 21px
    display: inline-block
    vertical-align: top
    cursor: pointer
    margin-left: 4px
  &.switch
    width: 60px
    height: 30px
    border-radius: 30px
    &:after
      left: 4px
      top: 3px
      border-radius: 50%
      width: 22px
      height: 22px
      background: var(--ab, var(--border))
      transform: translateX(var(--x, 0))
    &:checked
      --ab: var(--active-inner)
      --x: 28px
    &:disabled:not(:checked):after
      opacity: 0.6
